/*----------------------------------------*/
/* Template default CSS
/*----------------------------------------*/

html,
body {
  height: 100%;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  color: #111111;
  font-weight: 700;
}

h1 {
  font-size: 70px;
  font-family: Oswald, Roboto;
}

h2 {
  font-size: 36px;
  font-family: Oswald, Roboto;
}

h3 {
  font-size: 30px;
  font-family: Oswald, Roboto;
}

h4 {
  font-size: 24px;
  font-family: Oswald, Roboto;
}

h5 {
  font-size: 18px;
  font-family: Oswald, Roboto;
}

h6 {
  font-size: 16px;
  font-family: Oswald, Roboto;
}

p {
  font-size: 16px;
  color: #161616;
  line-height: 1.8;
}

img {
  max-width: 100%;
}

input:focus,
select:focus,
button:focus,
textarea:focus {
  outline: none;
}

a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

:root {
  --main-background-color: #e4e4e4;
  --main-text-color: #3d3d3d;
  --alt-text-color: #fff;
  --alt-color: #025892;
  --alt-background-color: #ab3b6b;
}

.alt-text-color {
  color: #fff;
  color: var(--alt-text-color);
}

.alt-background-color {
  background-color: #ab3b6b;
  background-color: var(--alt-background-color);
}

.navbar {
  background-color: #fff !important;
}

a {
  text-decoration: none !important;
}
/*---------------------
Helper CSS
 -----------------------*/

/* .main-color {
  color: var(--main-text-color);
} */

#root {
  background: #e4e4e4;
  background: var(--main-background-color);
  height: 100%;
}

.section-title {
  text-align: center;
}

.section-title h2 {
  font-size: 36px;
}

.set-bg {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.spad {
  padding-top: 106px;
  padding-bottom: 106px;
}

.text-white h1,
.text-white h2,
.text-white h3,
.text-white h4,
.text-white h5,
.text-white h6,
.text-white p,
.text-white span,
.text-white li,
.text-white a {
  color: #fff;
}

/*---------------------
Commom elements
 -----------------------*/

/* buttons */

.site-btn {
  display: inline-block;
  border: none;
  font-size: 14px;
  font-weight: 600;
  min-width: 164px;
  padding: 21px 47px;
  border-radius: 50px;
  text-transform: uppercase;
  background: #ab3b6b;
  background: var(--alt-background-color);
  color: #fff;
  color: var(--alt-text-color);
  line-height: 1;
  cursor: pointer;
  text-align: center;
}

.site-btn:hover {
  color: #c0c0c0;
}

.site-btn.sb-color {
  background: #ff1f56;
  color: #fff;
}

.site-btn.sb-color:hover {
  color: #fff;
}

/* Preloder */

#preloder {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999999;
  background: #000;
}

.loader {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -13px;
  margin-left: -13px;
  border-radius: 60px;
  animation: loader 0.8s linear infinite;
  -webkit-animation: loader 0.8s linear infinite;
}

@keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    border: 4px solid #f44336;
    border-left-color: transparent;
  }
  50% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
    border: 4px solid #673ab7;
    border-left-color: transparent;
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
    border: 4px solid #f44336;
    border-left-color: transparent;
  }
}

@-webkit-keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
    border: 4px solid #f44336;
    border-left-color: transparent;
  }
  50% {
    -webkit-transform: rotate(180deg);
    border: 4px solid #673ab7;
    border-left-color: transparent;
  }
  100% {
    -webkit-transform: rotate(360deg);
    border: 4px solid #f44336;
    border-left-color: transparent;
  }
}
.bg-dark {
  background-color: #353535;
}
/*------------------
Header section
 ---------------------*/

.header-section {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  background: #353535;
  z-index: 9999;
}

.site-logo {
  display: inline-block;
  padding: 21px 55px 20px;
  background: #222222;
}

.main-menu {
  display: inline-block;
  padding-left: 44px;
  position: relative;
  z-index: 99;
  background: #353535;
}

.main-menu li {
  display: inline-block;
}

.main-menu li a {
  position: relative;
  display: block;
  padding: 44px 7px;
  margin-right: 55px;
  font-size: 16px;
  font-weight: 500;
  color: #fff;
}

.main-menu li a:after {
  position: absolute;
  content: '';
  width: 0;
  height: 3px;
  left: 0;
  bottom: 0;
  background: #10ddb4;
  transition: all 0.3s;
}

.main-menu li a:hover:after {
  width: 100%;
}

.slicknav_menu {
  display: none;
}

.header-add {
  position: absolute;
  right: 0;
  top: 0;
  width: 43%;
  height: 100%;
  text-align: right;
}

.header-add img {
  height: 100%;
}

/*------------------
   Hero-section
 ---------------------*/

.hero-item {
  height: 804px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  padding-top: 140px;
}

.hero-item:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #0d0d11;
  opacity: 0.74;
  z-index: 1;
}

.hero-item .container {
  position: relative;
  z-index: 9;
}

.hero-item h2 {
  font-size: 96px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 15px;
}

.hero-item p {
  font-size: 20px;
  margin-bottom: 35px;
}

.hero-item .site-btn,
.hero-item p,
.hero-item h2 {
  position: relative;
  top: 50px;
  opacity: 0;
}

.hero-slider .owl-item.active .hero-item h2,
.hero-slider .owl-item.active .hero-item p,
.hero-slider .owl-item.active .hero-item .site-btn {
  top: 0;
  opacity: 1;
}

.hero-slider .owl-item.active .hero-item h2 {
  transition: all 0.5s ease 0.4s;
}

.hero-slider .owl-item.active .hero-item p {
  transition: all 0.5s ease 0.6s;
}

.hero-slider .owl-item.active .hero-item .site-btn {
  transition: all 0.5s ease 0.8s;
}

